import React from "react";
import styled from "@emotion/styled";

const AboutStyled = styled.div({
    margin: "10px 20px",
    display: "flex",
    flexDirection: "column",
});

const Title = styled.div({
    textAlign: "center",
    margin: "20px 10px",
    fontSize: "20px",
    fontWeight: "500",
});

function About() {
    return (
        <AboutStyled>
            <Title> </Title>
            <div>
                <p>
                    <span style={{ fontWeight: "500" }}>Bortik Project</span> -     ,    
                              .
                </p>
                <p>   -       .</p>
            </div>
        </AboutStyled>
    );
}

export default About;
