import React from "react";
import styled from "@emotion/styled";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import { useNavigate } from "react-router";
import { useDispatch } from "react-redux";
import { cleanCart } from "../store/manage";

const CartSentModalStyled = styled.div({});

function CartSentModal({ onHide, email, showDone, orderID, ...otherProps }) {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const close = () => {
        onHide();
        dispatch(cleanCart());
        navigate("/");
    };
    return (
        <CartSentModalStyled>
            {showDone === "OK" ? (
                <Modal {...otherProps} size="sm" aria-labelledby="contained-modal-title-vcenter" centered>
                    <Modal.Body>
                        <h5 style={{ textAlign: "center" }}> !</h5>
                        <p>
                               <b>{orderID}</b>.      email: <b>{email}</b>
                        </p>
                        <p>           .</p>
                    </Modal.Body>
                    <Modal.Footer style={{ display: "flex", justifyContent: "center" }}>
                        <Button onClick={close}> </Button>
                    </Modal.Footer>
                </Modal>
            ) : null}
            {showDone === "Error" ? (
                <Modal {...otherProps} size="sm" aria-labelledby="contained-modal-title-vcenter" centered>
                    <Modal.Body>
                        <h5 style={{ textAlign: "center" }}> !</h5>
                        <p>   .</p>
                        <p>      .</p>
                    </Modal.Body>
                    <Modal.Footer style={{ display: "flex", justifyContent: "center" }}>
                        <Button onClick={() => onHide()}></Button>
                    </Modal.Footer>
                </Modal>
            ) : null}
        </CartSentModalStyled>
    );
}

export default CartSentModal;
